<template>
  <f7-page @page:beforeremove="onPageBeforeRemove">
    <f7-navbar title="//get_unstuck" back-link="Back">
      <f7-nav-right>
        <f7-link icon-f7="trash" class="icon-black" v-on:click="removeTodo"></f7-link>
      </f7-nav-right>
    </f7-navbar>

    <f7-card v-if="!showAddTodo">
      <f7-card-header>Is your next step obvious?</f7-card-header>
      <f7-card-footer>
        <f7-link v-on:click="yes()">Yes</f7-link>
        <f7-link v-on:click="no()">No</f7-link>
      </f7-card-footer>
    </f7-card>

    <f7-card v-if="showAddTodo">
      <f7-list inset class="m-0">
        <f7-list-item>
          <f7-label>Add todo step.</f7-label>
          <f7-input class="custom-input" type="text" placeholder="Ask a question. Be specific." @input="formData.name = $event.target.value" @focus="inputFocus" :value="formData.name" required clear-button></f7-input>
        </f7-list-item>
        <f7-list-item>
          <f7-button v-on:click="addStep()">Add step</f7-button>
        </f7-list-item>
      </f7-list>
    </f7-card>

    <f7-block class="help text-color-black" v-if="showAddTodo && (this.steps === 0 || focus)">
      <p>What isn't obvious? What do you need to figure out.</p>
      <p>Create a todo step and ask a question. Make it as specific as you can.</p>
    </f7-block>

    <f7-block-title v-if="steps" class="text-color-black note">{{title}}</f7-block-title>

    <f7-card v-if="steps">
      <f7-list>
        <f7-progressbar :progress="completed"></f7-progressbar>

        <f7-list-item class="litem" swipeout
                      v-for="(step, index) in steps" link="#" after="Answer"
                      v-on:click="openStepModal(step)"
                      :key="index"
                      :title="step.name"
                      :footer="step.details"
                      :class="step.completed ? 'completed' : ''">
          <f7-icon slot="media" :f7="step.completed ? 'check_round' : 'bolt'"></f7-icon>
          <f7-swipeout-actions left>
            <f7-swipeout-button close @click="onComplete(step.key)" color="green">Complete</f7-swipeout-button>
            <f7-swipeout-button close @click="onNotComplete(step.key)" color="blue">Not Complete</f7-swipeout-button>
          </f7-swipeout-actions>
          <f7-swipeout-actions right>
            <f7-swipeout-button close @click="onDeleted(step.key)" color="red">Delete</f7-swipeout-button>
          </f7-swipeout-actions>
        </f7-list-item>
      </f7-list>
    </f7-card>

    <f7-block v-if="steps" class="text-color-black">
      <f7-icon f7="info" class="small-info-icon"></f7-icon>
      <strong>Tip:</strong> Swipe item for more actions.
    </f7-block>

    <f7-popup class="todo-step-popup" :opened="todoStepPopupOpened" @popup:closed="todoStepPopupOpened = false">
      <f7-page class="page-bg" v-if="step">
        <f7-navbar :title="step.name">
          <f7-nav-right>
            <f7-link popup-close>Close</f7-link>
          </f7-nav-right>
        </f7-navbar>
        <f7-card>
          <f7-list inset class="m-0">
            <f7-list-item>
              <f7-label>Step details. Answer your question.</f7-label>
              <f7-input type="textarea" placeholder="..." @input="formData.details = $event.target.value" :value="formData.details" required clear-button></f7-input>
            </f7-list-item>
            <f7-list-item>
              <f7-button v-on:click="addStepDetails()">Add details</f7-button>
            </f7-list-item>
          </f7-list>
        </f7-card>

        <f7-block-title>Current details</f7-block-title>
        <f7-block class="note">{{step.details}}</f7-block>
      </f7-page>
    </f7-popup>
  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/auth';
  import 'firebase/database';
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        uid: localStorage.getItem('uid'),
        key: this.$f7route.params.key,
        title: '//todo',
        showAddTodo: false,
        todoStepPopupOpened: false,
        focus: false,
        steps: null,
        todo: null,
        step: null,
        completed: 0,
        formData: {name: '', details: ''}
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.getSteps();
      })
    },
    methods: {
      getSteps() {
        Firebase.database().ref('todos/' + this.key).on('value', (resp) => {
          let todo = resp.val();

          if (todo) {
            this.title = todo.title;
            this.todo = todo;

            if (todo.steps !== undefined) {
              this.showAddTodo = true;
              let sorted = main.sortSteps(todo.steps);

              this.steps = sorted.steps;
              this.completed = sorted.completed;
            }
          }
        });
      },

      inputFocus() {
        this.focus = true;
      },

      addStep() {
        if (this.formData.name.trim() === '') {
          this.$f7.dialog.alert('Your step need a name');
          return false;
        }

        main.createNewStep({key: this.key, name: this.formData.name.trim()}).then((key) => {
          this.formData.name = '';
          this.focus = false;
        });
      },

      openStepModal(step) {
        this.step = step;

        this.$f7.popup.open('.todo-step-popup');
      },

      addStepDetails() {
        if (this.formData.details.trim() === '') {
          this.$f7.dialog.alert('Details cannot be empty');
          return false;
        }

        main.addStepDetails(this.key, this.step.key, this.formData.details.trim()).then((key) => {
          this.formData.details = '';

          this.$f7.popup.close();
        });
      },

      no() {
        this.showAddTodo = true;
      },

      yes() {
        this.$f7.dialog.preloader();
        main.markTodoCompleted(this.key, true).then(() => {
          this.$f7.dialog.close();
          this.$f7.views.current.router.navigate('/');
        });
      },

      onDeleted(key) {
        this.$f7.dialog.preloader();
        main.markStepDeleted(this.key, key, true).then(() => {
          this.$f7.dialog.close();
        });
      },

      removeTodo() {
        this.$f7.dialog.preloader();
        main.deleteUserTodo(this.key).then(() => {
          main.deleteTodo(this.key).then(() => {
            this.$f7.dialog.close();
            this.$f7.views.current.router.navigate('/');
          });
        });
      },

      onNotDeleted(key) {
        this.$f7.dialog.preloader();
        main.markStepDeleted(this.key, key, false).then(() => {
          this.$f7.dialog.close();
        });
      },

      onComplete(key) {
        this.$f7.dialog.preloader();
        main.markStepComplete(this.key, key, true).then(() => {
          this.$f7.dialog.close();
        });
      },

      onNotComplete(key) {
        this.$f7.dialog.preloader();
        main.markStepComplete(this.key, key, false).then(() => {
          this.$f7.dialog.close();
        });
      },

      onPageBeforeRemove() {
        if (this.$f7.popup) this.$f7.popup.destroy('.todo-step-popup');
      },

      editStep() {
      }
    }
  }
</script>



