<template>
  <f7-page>
    <f7-navbar ref="navbar">
      <f7-nav-left>
        <f7-link icon-if-ios="f7:home" icon-if-md="material:home" class="icon-white" v-on:click="goToHomePage"></f7-link>
      </f7-nav-left>
      <f7-nav-title>//get_unstuck profile</f7-nav-title>
      <f7-nav-right>
        <f7-link icon-if-ios="f7:info" icon-if-md="material:info" class="icon-white" v-on:click="goToAboutPage"></f7-link>
      </f7-nav-right>
    </f7-navbar>

    <f7-card>
      <f7-list inset class="m-0">
        <f7-list-item>
          <f7-label>Name</f7-label>
          <f7-input type="text" placeholder="..." @input="formData.username = $event.target.value" :value="formData.username" required clear-button></f7-input>
        </f7-list-item>
        <f7-list-item>
          <f7-label>Skills</f7-label>
          <f7-input type="text" placeholder="engineer, web developer, photographer, ..." @input="formData.skills = $event.target.value" :value="formData.skills" required clear-button></f7-input>
        </f7-list-item>
        <f7-list-item>
          <f7-button v-on:click="updateProfile()">Update Profile</f7-button>
        </f7-list-item>
      </f7-list>
    </f7-card>
    <f7-card v-if="skills">
      <f7-list>
        <f7-list-item swipeout v-for="(skill, index) in skills" :key="index" :title="index">
          <f7-swipeout-actions right>
            <f7-swipeout-button delete close @click="onDeleted(index)">Delete</f7-swipeout-button>
          </f7-swipeout-actions>
        </f7-list-item>
      </f7-list>
    </f7-card>
    <f7-block class="text-color-black"><strong>Tip:</strong> Swipe item for more actions.</f7-block>
  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/auth';
  import 'firebase/database';
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        uid: localStorage.getItem('uid'),
        type: this.$f7route.params.type,
        user: null,
        skills: null,
        formData: {username: '', skills: ''}
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.getProfile();
        this.getSkills();
      })
    },
    methods: {
      getProfile() {
        Firebase.database().ref('users/' + this.uid).on('value', (resp) => {
          this.user = resp.val();

          this.formData.username = this.user.username;
        });
      },

      getSkills() {
        Firebase.database().ref('users_skills/' + this.uid).on('value', (resp) => {
          this.skills = resp.val();
        });
      },

      async updateProfile() {
        if (this.formData.username.trim() === '') {
          this.$f7.dialog.alert('Name is needed.');
          return false;
        }

        if (this.formData.skills.trim() === '') {
          this.$f7.dialog.alert('Phone number is required.');
          return false;
        }

        let username = this.formData.username.trim();
        let skillArray = this.formData.skills.split(',').map(Function.prototype.call, String.prototype.trim);

        //  update username
        await main.updateProfile({uid: this.user.uid, username: username});

        // create skills
        let skillsPromises = [];
        for (let i = 0; i < skillArray.length; i++) {
          skillsPromises.push(main.addSkill({uid: this.user.uid, skill: skillArray[i]}));
        }

        Promise.all(skillsPromises).then(() => {
          this.formData.skills = '';
        });

      },

      async onDeleted(key) {
        await main.deleteSkill(this.uid, key);
      },

      goToAboutPage() {
        this.$f7.views.current.router.navigate('/about/');
      },

      goToHomePage() {
        this.$f7.views.current.router.navigate('/');
      }
    }
  }
</script>



