// Import F7 Styles
import Framework7Styles from 'framework7/css/framework7.css';

import Firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/database';

import FirebaseUi from 'firebaseui';

// Import Icons and App Custom Styles
import IconsStyles from './css/icons.css';
import AppStyles from './css/app.css';

let config = {
  apiKey: "AIzaSyDkjqHYk9pVYBBDtpVe8G_i-wcxD6cgpS8",
  authDomain: "todo-4473a.firebaseapp.com",
  databaseURL: "https://todo-4473a.firebaseio.com",
  projectId: "todo-4473a",
  storageBucket: "todo-4473a.appspot.com",
  messagingSenderId: "11009476936"
};

Firebase.initializeApp(config);

// Import App Component
import App from './app';

// Init F7 Vue Plugin
Framework7.use(Framework7Vue);

// Init App
new Vue({
  el: '#app',
  template: '<app/>',

  // Register App Component
  components: {
    app: App
  }
});



