<template>
  <f7-page>
    <f7-navbar ref="navbar">
      <f7-nav-left>
        <f7-link icon-if-ios="f7:home" icon-if-md="material:home" class="icon-white" v-on:click="goToHomePage"></f7-link>
      </f7-nav-left>
      <f7-nav-title>//get_unstuck</f7-nav-title>
      <f7-nav-right>
        <f7-link icon-if-ios="f7:info" icon-if-md="material:info" class="icon-white" v-on:click="goToAboutPage"></f7-link>
      </f7-nav-right>

      <f7-subnavbar :inner="false">
        <f7-searchbar disable-link-text="Cancel" v-on:input="onSearchInput($event, that)" placeholder="Search" :clear-button="true"></f7-searchbar>
      </f7-subnavbar>
    </f7-navbar>

    <f7-card v-if="todos && !todo.isCompleted" v-for="(todo, index) in todos" :title="todo.title" :key="index">
      <f7-card-content :padding="false">
        <f7-list v-if="todo.steps && todo.steps.length">
          <f7-list-item v-for="(step, i) in todo.steps"
                        v-if="step.completed === false && step.deleted === false && i < 2"
                        :key="i"
                        :title="step.name"></f7-list-item>
        </f7-list>
      </f7-card-content>
      <f7-card-footer>
        <span>{{todo.date.m}} {{todo.date.day}} {{todo.date.year}}</span>
        <span v-on:click="viewTodo(todo)">{{todo.comments ? todo.comments : '0'}} comments</span>
        <f7-button v-on:click="viewTodo(todo)">View</f7-button>
      </f7-card-footer>
    </f7-card>

    <f7-card v-if="showModeButton">
      <f7-button v-on:click="loadMoreTodos">Load more</f7-button>
    </f7-card>

    <f7-popup :opened="popupOpened" @popup:closed="popupOpened = false">
      <f7-page v-if="todo">
        <f7-navbar :title="todo.title">
          <f7-nav-right>
            <f7-link popup-close>Close</f7-link>
          </f7-nav-right>
        </f7-navbar>

        <div class="bottom-margin">
          <f7-list class="m-0" v-if="todo.steps && todo.steps.length">
            <f7-list-item v-for="(step, i) in todo.steps"
                          v-if="step.completed === false && step.deleted === false"
                          :key="i"
                          :title="step.name"></f7-list-item>
          </f7-list>

          <f7-card class="cus-card-bg note" v-if="comments" v-for="(comment, i) in comments" :key="i" :content="comment.comment">
            <f7-card-footer>
              <span><f7-icon f7="person"></f7-icon> {{comment.user.username}}</span>
              <span>{{comment.date.m}} {{comment.date.day}} {{comment.date.year}}</span>
            </f7-card-footer>
          </f7-card>
        </div>

        <div class="bottom-fixed">
          <f7-card>
            <f7-list class="m-0">
              <f7-list-item>
                <f7-input type="textarea" resizable placeholder="..." @input="formData.comment = $event.target.value" :value="formData.comment" required clear-button></f7-input>
              </f7-list-item>
              <f7-list-item>
                <f7-button v-on:click="addTodoComment()">Add comment</f7-button>
              </f7-list-item>
            </f7-list>
          </f7-card>
        </div>
      </f7-page>
    </f7-popup>

  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/auth';
  import 'firebase/database';
  import debounce from 'debounce';
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        uid: localStorage.getItem('uid'),
        type: this.$f7route.params.type,
        that: this,
        len: 50,
        todos: null,
        todo: null,
        comments: null,
        popupOpened: false,
        showModeButton: true,
        formData: {comment: ''}
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.getTodo();
      })
    },
    methods: {

      getTodo() {
        this.$f7.dialog.preloader();

        Firebase.database().ref('todos').orderByChild('public').equalTo(true).limitToFirst(this.len).once('value').then((resp) => {
          if (resp.val()) {
            this.todos = main.todosToArray(resp.val());
            this.length = Object.keys(this.todos).length;
            this.showModeButton = this.length === this.len;
          }

          this.$f7.dialog.close();
        });
      },

      getComments() {
        if (!this.todo) return false;

        this.$f7.dialog.preloader();

        Firebase.database().ref('comments/' + this.todo.key).on('value', (resp) => {
          if (resp.val()) {
            console.log(resp.val());
            main.getCommentsAndUsers(resp.val()).then((resp) => {
              this.comments = resp;

              main.updateTodoCount(this.todo.key, Object.keys(this.comments).length).then(() => {
                this.$f7.dialog.close();
              });
            });
          } else {
            this.$f7.dialog.close();
          }
        });
      },

      loadMoreTodos() {
        let lastKey = this.todos[this.todos.length - 1]['key'];

        this.$f7.dialog.preloader();
        Firebase.database().ref('todos').orderByChild('key').limitToFirst(this.len + 1).startAt(lastKey).once('value').then((resp) => {
          if (resp.val()) {
            let todos = main.todosToArray(resp.val());

            for (let i = 1; i < todos.length; i++) {
              if (todos[i].public === true) {
                this.todos.push(todos[i]);
              }
            }

            this.showModeButton = todos.length === this.len + 1;
          }

          this.$f7.dialog.close();
        });

        return false;
      },

      onSearchInput: debounce((e, that) => {
        let val = e.target.value;

        Firebase.database().ref('todos').orderByChild('title').startAt(val).endAt(val + "\uf8ff").once('value').then((resp) => {
          if (resp.val()) {
            let todos = main.todosToArray(resp.val());
            that.todos = [];
            for (let i = 0; i < todos.length; i++) {
              that.todos.push(todos[i]);
            }
          }
        });
      }, 250),

      addTodoComment() {
        if (this.formData.comment.trim() === '') {
          this.$f7.dialog.alert('Comment cannot be empty');
          return false;
        }

        main.addNewComment(this.todo.key, this.formData.comment, this.uid).then(() => {
          this.formData.comment = '';
        });
      },

      viewTodo(todo) {
        this.todo = todo;
        this.popupOpened = true;

        this.getComments();
      },

      goToAboutPage() {
        this.$f7.views.current.router.navigate('/about/');
      },

      goToHomePage() {
        this.$f7.views.current.router.navigate('/');
      }
    }
  }
</script>



