<template>
  <f7-page @page:beforein="onPageBeforeIn" @page:beforeout="onPageBeforeOut">
    <f7-navbar ref="navbar">
      <f7-nav-left>
        <f7-link icon-if-ios="f7:person" icon-if-md="material:person" class="icon-white" v-on:click="goToProfilePage"></f7-link>
      </f7-nav-left>
      <f7-nav-title>//get_unstuck</f7-nav-title>
      <f7-nav-right>
        <f7-link icon-if-ios="f7:search" icon-if-md="material:search" class="icon-white" v-on:click="search"></f7-link>
      </f7-nav-right>
      <f7-nav-right>
        <f7-link icon-if-ios="f7:info" icon-if-md="material:info" class="icon-white" v-on:click="goToAboutPage"></f7-link>
      </f7-nav-right>
    </f7-navbar>

    <f7-block ref="top" class="mb-15">
      <h3 class="text-color-black m-0">{{date.m}} {{date.day}} {{date.year}}</h3>
      <h2 v-if="todos" class="text-color-black m-0">{{completedTodos}} out of {{totalTodos}} completed.</h2>
    </f7-block>

    <f7-fab v-if="!todos" class="mt-5" position="center-center" slot="fixed" color="green" text="About this app" v-on:click="goToAboutPage">
      <f7-icon f7="info" class="text-color-white"></f7-icon>
    </f7-fab>

    <f7-block v-if="!todos" class="start-here"><h3 class="text-color-black m-0">Start here</h3></f7-block>

    <f7-fab class="todo-fab" position="right-bottom" slot="fixed" v-on:click="openTodoModal">
      <f7-icon ios="f7:add" md="material:add"></f7-icon>
    </f7-fab>

    <f7-swiper pagination v-if="todos" :params="{on: {init: swiperIsReady}}">
      <f7-swiper-slide v-for="(todo, index) in todos" :key="index">
        <f7-card class="custom-height-card">
          <f7-card-content>
            <f7-row class="v-center">
              <f7-col>
                <f7-block-title class="m-0">{{todo.title}}</f7-block-title>
              </f7-col>
              <f7-col class="text-align-right block-title-color">Ask for help
                <f7-toggle :checked="todo.public" v-on:change="makePublic($event, todo.key)"></f7-toggle>
              </f7-col>
            </f7-row>
          </f7-card-content>

          <f7-progressbar class="progressbar" :progress="todo.completed"></f7-progressbar>

          <f7-list v-if="todo.steps" class="swiper-list">
            <f7-list-item class="litem" link="#" after="Answer"
                          v-on:click="openStepModal(todo, step)"
                          v-for="(step, index) in todo.steps"
                          :key="index"
                          :title="step.name"
                          :footer="step.details"
                          :class="step.completed ? 'completed' : ''">
              <f7-icon slot="media" :f7="step.completed ? 'check_round' : 'circle'"></f7-icon>
            </f7-list-item>
          </f7-list>

          <f7-card-content class="view-all">
            <f7-button v-on:click="gotoTodo(todo.key)">View All</f7-button>
          </f7-card-content>
        </f7-card>
      </f7-swiper-slide>
    </f7-swiper>

    <f7-popup class="todo-popup" :opened="todoPopupOpened" @popup:closed="todoPopupOpened = false">
      <f7-page class="page-bg">
        <f7-navbar title="Name your problem">
          <f7-nav-right>
            <f7-link popup-close>Close</f7-link>
          </f7-nav-right>
        </f7-navbar>
        <f7-card>
          <f7-list class="m-0">
            <f7-list-item>
              <f7-label>Name your problem</f7-label>
              <f7-input class="custom-input" type="text" placeholder="..." @input="formData.title = $event.target.value" :value="formData.title" required clear-button></f7-input>
            </f7-list-item>
            <f7-list-item>
              <f7-button v-on:click="getUnstuck()">Get Unstuck</f7-button>
            </f7-list-item>
          </f7-list>
        </f7-card>
        <f7-card>
          <f7-block strong>
            <p>When you are trying to solve a problem, if your next step is obvious, then do it.</p>
            <p>Most often this is not the case. When that happens, take a step back and think what it is that you need to figure out, or do, or work through next.</p>
            <p>Write down a question about a narrow part of that. Make it as specific as you can.</p>
          </f7-block>
        </f7-card>
      </f7-page>
    </f7-popup>

    <f7-popup class="step-popup" :opened="stepPopupOpened" @popup:closed="stepPopupOpened = false">
      <f7-page class="page-bg" v-if="step">
        <f7-navbar :title="step.name">
          <f7-nav-right>
            <f7-link popup-close>Close</f7-link>
          </f7-nav-right>
        </f7-navbar>
        <f7-card>
          <f7-list class="m-0">
            <f7-list-item>
              <f7-label>Step details. Answer your question.</f7-label>
              <f7-input class="custom-input" type="textarea" placeholder="..." @input="formData.details = $event.target.value" :value="formData.details" required clear-button></f7-input>
            </f7-list-item>
            <f7-list-item>
              <f7-button v-on:click="addStepDetails()">Add details</f7-button>
            </f7-list-item>
          </f7-list>
        </f7-card>

        <f7-block-title>Current details</f7-block-title>
        <f7-block strong inset>{{step.details}}</f7-block>
      </f7-page>
    </f7-popup>

  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/auth';
  import 'firebase/database';
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        uid: localStorage.getItem('uid'),
        phone: localStorage.getItem('phone'),
        date: main.timeConvert(),
        completedTodos: 0,
        totalTodos: 0,
        todos: null,
        todo: null,
        step: null,
        todoPopupOpened: false,
        stepPopupOpened: false,
        formData: {title: '', details: '', public: false},
        getTodoCaller: false
      }
    },
    mounted() {
      this.$f7ready(() => {
        if (this.getTodoCaller === false) this.getTodo();
      })
    },
    methods: {
      onPageBeforeIn() {
        if (this.getTodoCaller === false) this.getTodo();
      },

      onPageBeforeOut() {
        this.getTodoCaller = false;
        if (this.$f7.popup) this.$f7.popup.destroy('.step-popup');
      },

      getTodo() {
        this.getTodoCaller = true;
        this.$f7.dialog.preloader();

        Firebase.database().ref('users/' + this.uid).on('value', (resp) => {
          let user = resp.val();
          let todos = user.todos === undefined ? null : user.todos;

          if (todos) {
            let todosPromiseArray = [];
            for (let key in todos) {
              if (todos.hasOwnProperty(key)) todosPromiseArray.push(main.getTodo(key));
            }

            Promise.all(todosPromiseArray).then((todoResp) => {
              let todoItems = [];
              for (let t = 0; t < todoResp.length; t++) {

                let todoItem = todoResp[t].val();
                let sorted = main.sortSteps(todoItem.steps);

                // set completed todos
                if (sorted.completed === 100) {
                  this.completedTodos++;
                }

                todoItem.key = todoResp[t].key;
                todoItem.steps = sorted.steps;
                todoItem.completed = sorted.completed;

                todoItems.push(todoItem);
              }

              this.todos = todoItems.reverse();
              this.totalTodos = todoItems.length;

              this.$f7.dialog.close();
            });
          } else {
            this.$f7.dialog.close();
          }
        });
      },

      swiperIsReady() {
        this.$nextTick().then(() => {
          let height = this.$f7.height - this.$refs.top.$el.clientHeight - this.$refs.navbar.$el.clientHeight - (this.$device.ios ? 240 : 180);

          document.querySelectorAll('.swiper-list').forEach(el => {
            el.style.height = height + 'px'
          });
        });

      },

      async makePublic(event, key) {
        await main.makeTodoPublic(key, event.target.checked);
      },

      getUnstuck() {
        if (this.formData.title.trim() === '') {
          this.$f7.dialog.alert('Your problem need a title');
          return false;
        }

        main.createNewTodo({title: this.formData.title.trim(), details: this.formData.details.trim()}).then((key) => {
          this.formData.title = '';
          this.formData.details = '';

          this.$f7.popup.close();
          this.$f7.views.current.router.navigate('/todo/' + key);
        });
      },

      addStepDetails() {
        if (this.formData.details.trim() === '') {
          this.$f7.dialog.alert('Details cannot be empty');
          return false;
        }

        main.addStepDetails(this.todo.key, this.step.key, this.formData.details.trim()).then(() => {
          this.formData.details = '';

          this.$f7.popup.close();
        });
      },

      openTodoModal() {
        this.$f7.popup.open('.todo-popup');
      },

      openStepModal(todo, step) {
        this.todo = todo;
        this.step = step;

        this.$nextTick(() => {
          this.$f7.popup.open('.step-popup');
        });
      },

      search() {
        this.$f7.views.current.router.navigate('/search/');
      },

      gotoTodo(key) {
        this.$f7.views.current.router.navigate('/todo/' + key);
      },

      goToAboutPage() {
        this.$f7.views.current.router.navigate('/about/');
      },

      goToProfilePage() {
        this.$f7.views.current.router.navigate('/profile/update');
      }
    }
  }
</script>



