<template>
  <f7-page>
    <f7-navbar ref="navbar">
      <f7-nav-left>
        <f7-link icon-if-ios="f7:home" icon-if-md="material:home" class="icon-white" v-on:click="goToHomePage"></f7-link>
      </f7-nav-left>
      <f7-nav-title>//get_unstuck about</f7-nav-title>
      <f7-nav-right>
        <f7-link icon-if-ios="f7:person" icon-if-md="material:person" class="icon-white" v-on:click="goToProfilePage"></f7-link>
      </f7-nav-right>
    </f7-navbar>

    <f7-toolbar tabbar class="bg-green">
      <f7-link tab-link="#tab-1" tab-link-active>About</f7-link>
      <f7-link tab-link="#tab-2">F.A.Q</f7-link>
    </f7-toolbar>
    <f7-tabs>
      <f7-tab id="tab-1" class="page-content p-0" tab-active>
        <f7-block-title class="text-color-black">The philosophy</f7-block-title>
        <f7-card>
          <f7-block strong>
            <p>When you are trying to solve a problem, if your next step is obvious, then do it.</p>
            <p>Most often this is not the case. When that happens, take a step back and think what it is that you need to figure out, or do, or work through next.</p>
            <p>Write down a question about a narrow part of that. Make it as specific as you can. Then answer that question.</p>
            <p>Repeat.</p>
          </f7-block>
        </f7-card>

        <f7-block-title class="text-color-black">For example</f7-block-title>
        <f7-card>
          <f7-block strong>
            <p>If you ask me to build you a planter box, there are a lot of things that I don't know.</p>
            <ul>
              <li>I don't know what wood to buy.</li>
              <li>I don't know what dimensions you want it.</li>
              <li>I don't know how where you live.</li>
            </ul>
            <p>It can be overwhelming to know where to start.</p>
            <p>In this case, I take a step back and focus on a single piece of this that I don't know.</p>
            <p>Once I solve one step, I can move to the next one.</p>
          </f7-block>
        </f7-card>

        <f7-block-title class="text-color-black">Solving the problem</f7-block-title>
        <f7-card>
          <f7-block strong>
            <p>Often you will have multiple questions in your mind about your problem, and can be difficult to focus.</p>
            <p>Having everything written down will help solve your focus, and given enough time, you will solve the problem.</p>
          </f7-block>
        </f7-card>
      </f7-tab>
      <f7-tab id="tab-2" class="page-content p-0">
        <f7-block-title class="text-color-black">What is <strong>Ask for help</strong>?</f7-block-title>
        <f7-card>
          <f7-block strong>
            <p>By enabling the <strong>Ask for help</strong> feature, your entry will become publicly visible and searchable to other users.</p>
            <p>Other users will be able to give input and possibly help in any way.</p>
          </f7-block>
        </f7-card>
      </f7-tab>
    </f7-tabs>
  </f7-page>
</template>

<script>
  export default {
    methods: {
      goToHomePage() {
        this.$f7.views.current.router.navigate('/');
      },

      goToProfilePage() {
        this.$f7.views.current.router.navigate('/profile/update');
      }
    }
  }
</script>



