import Firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/database';

export class Main {

  async getUser(id) {
    return new Promise((resolve) => {
      Firebase.database().ref('users/' + id).once('value').then((resp) => {
        resolve(resp)
      });
    });
  }

  async updateProfile(obj) {
    return new Promise((resolve) => {
      Firebase.database().ref('users/' + obj.uid).update({username: obj.username}).then((resp) => {
        resolve(resp)
      });
    });
  }

  async deleteSkill(uid, skill) {
    return new Promise((resolve) => {
      Firebase.database().ref('users_skills/' + uid + '/' + skill).remove().then((resp) => {
        resolve(resp);
      });
    });
  }

  async getSkill(skill) {
    return new Promise((resolve) => {
      Firebase.database().ref('skills/' + skill).once('value').then((resp) => {
        resolve(resp);
      });
    });
  }

  async addNewSkill(uid, skill) {
    return new Promise((resolve) => {
      Firebase.database().ref('users_skills/' + uid + '/' + skill).set(uid).then((resp) => {
        resolve(resp);
      });
    });
  }

  async addNewComment(todoKey, comment, uid) {
    let date = this.timeConvert();

    return new Promise((resolve) => {
      let key = Firebase.database().ref().child('comments').push().key;
      Firebase.database().ref('comments/' + todoKey + '/' + key).set({comment: comment, uid: uid, date: date}).then((resp) => {
        resolve(resp);
      });
    });
  }

  async addSkill(obj) {
    return new Promise((resolve) => {
      this.getSkill(obj.skill).then(resp => {
        if (resp.val() === null) {
          this.addNewSkill(obj.uid, obj.skill).then(() => {
            resolve(obj);
          });
        } else {
          resolve(obj);
        }
      });
    });
  }

  async createNewTodo(obj) {
    let date = this.timeConvert();

    return new Promise((resolve) => {
      let key = Firebase.database().ref().child('todo').push().key;

      Firebase.database().ref('users/' + this.getUid() + '/todos/' + key).set({active: true}).then(() => {
        Firebase.database().ref('todos/' + key).set({
          key: key,
          title: obj.title,
          details: obj.details,
          completed: false,
          deleted: false,
          public: false,
          timestamp: date.timestamp,
          date: date
        }).then(() => {
          resolve(key);
        });
      });
    });
  }

  async getTodo(key) {
    return new Promise((resolve) => {
      Firebase.database().ref('todos/' + key).once('value').then((resp) => {
        resolve(resp);
      });
    });
  }

  async addStepDetails(todoKey, stepKey, details) {
    return new Promise((resolve) => {
      Firebase.database().ref('todos/' + todoKey + '/steps/' + stepKey + '/details').set(details).then((resp) => {
        resolve(resp);
      });
    });
  }

  async markTodoCompleted(key, action) {
    return new Promise((resolve) => {
      Firebase.database().ref('todos/' + key + '/completed').set(action).then((resp) => {
        resolve(resp);
      });
    });
  }

  async markTodoDeleted(key, action) {
    return new Promise((resolve) => {
      Firebase.database().ref('todos/' + key + '/deleted').set(action).then((resp) => {
        resolve(resp);
      });
    });
  }

  async deleteTodo(key) {
    return new Promise((resolve) => {
      Firebase.database().ref('todos/' + key).remove().then((resp) => {
        resolve(resp);
      });
    });
  }

  async deleteUserTodo(key) {
    return new Promise((resolve) => {
      Firebase.database().ref('users/' + this.getUid() + '/todos/' + key).remove().then((resp) => {
        resolve(resp);
      });
    });
  }

  async updateTodoCount(key, length) {
    return new Promise((resolve) => {
      Firebase.database().ref('todos/' + key + '/comments').set(length).then((resp) => {
        resolve(resp);
      });
    });
  }

  async makeTodoPublic(key, pub) {
    return new Promise((resolve) => {
      Firebase.database().ref('todos/' + key + '/public').set(pub).then((resp) => {
        resolve(resp);
      });
    });
  }

  async createNewStep(obj) {
    let date = this.timeConvert();

    return new Promise((resolve) => {
      let key = Firebase.database().ref().child('todo').push().key;
      Firebase.database().ref('todos/' + obj.key + '/steps/' + key).set({
        name: obj.name,
        completed: false,
        deleted: false,
        date: date
      }).then(() => {
        this.markTodoCompleted(obj.key, false).then(() => resolve(key));
      });
    });
  }

  async markStepComplete(mainKey, key, action) {
    console.log(mainKey, key);
    return new Promise((resolve) => {
      Firebase.database().ref('todos/' + mainKey + '/steps/' + key + '/completed').set(action).then((resp) => {
        resolve(resp);
      });
    });
  }

  async markStepDeleted(mainKey, key, action) {
    return new Promise((resolve) => {
      Firebase.database().ref('todos/' + mainKey + '/steps/' + key + '/deleted').set(action).then((resp) => {
        resolve(resp);
      });
    });
  }

  todosToArray(todos) {
    let todosArray = [];

    for (let key in todos) {
      if (todos.hasOwnProperty(key)) {
        let sorted = this.sortSteps(todos[key].steps);
        todos[key].steps = sorted.steps;
        todos[key].isCompleted = parseInt(sorted.completed) >= 100 || todos[key].completed;
        todos[key].completed = parseInt(sorted.completed);

        todosArray.push(todos[key]);
      }
    }

    return todosArray;
  }

  async getCommentsAndUsers(comments) {
    let users = [];

    // get users
    for (let key in comments) {
      if (comments.hasOwnProperty(key) && comments[key].uid) {
        users.push(this.getUser(comments[key].uid));
      }
    }

    // once we have all users
    return Promise.all(users).then((resp) => {
      for (let u = 0; u < resp.length; u++) {
        let user = resp[u].val();

        // go through the comments and assign its user to its comment
        for (let k in comments) {
          if (comments.hasOwnProperty(k) && comments[k].uid === user.uid) {
            comments[k].user = user;
          }
        }
      }

      return comments;
    });
  }

  sortSteps(steps) {
    let completed = [];
    let notCompleted = [];
    let i = 0;
    let j = 0;

    if (steps) {
      for (let k in steps) {
        if (steps.hasOwnProperty(k)) {
          if (steps[k].deleted === false) {
            steps[k].key = k;

            if (steps[k].completed === true) {
              completed.push(steps[k]);
              i++;
              j++;
            }

            if (steps[k].completed === false) {
              notCompleted.push(steps[k]);
              j++;
            }
          }
        }
      }

      let percent = 100 - Math.round((j - i) * 100 / j);

      return {steps: notCompleted.concat(completed), completed: percent};
    }

    return {steps: [], completed: 100};
  }

  timeConvert(timestamp = Math.floor(Date.now())) {
    let a = new Date(timestamp);
    let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    let year = a.getFullYear();
    let month = a.getMonth();
    let m = months[month];
    let date = a.getDate();
    let hour = a.getHours();
    let min = a.getMinutes();
    let sec = a.getSeconds();

    return {
      month: ('0' + month).slice(-2),
      m: m,
      day: ('0' + date).slice(-2),
      year: year,
      hour: hour,
      min: min,
      sec: sec,
      timestamp: timestamp,
      date: year + '-' + ('0' + month).slice(-2) + '-' + ('0' + date).slice(-2)
    };
  }

  getUid() {
    return localStorage.getItem('uid');
  }
}

export const main = new Main();



